  font-style: inherit;
  font-weight: inherit;
  color: var(--color-signal);
}

h1 strong, h2 strong, h3 strong, h4 strong {
  color: var(--color-three);
}

.content :is(h2, h3, h4) {
  margin-left: calc(0rem - var(--text-indent));
}

